import React, { Component } from 'react';
import { Link } from 'react-router-dom';
// import firebase from '../../firebase.js';

import './item.sass';


export default class Item extends Component {

  render() {

    return (
      <div>
        <div className="card">
          <div className="fav-check"></div>
          <Link to='/item-page/31003'>
            <div className="img-wrapper">
              <img
                src="https://i.citrus.ua/imgcache/size_800/uploads/shop/d/1/d1c15eb8939eec3bccfe07c4a9acd1cd.jpg"
                alt="product"/>
             </div>
          </Link>
          <div className="card-body">
            <Link to='/item-page/31003'>Apple MacBook Pro Touch Bar 15" 512Gb Silver (MR972) 2018</Link>
          </div>
          <div className="price">79 999 грн</div>
          <div className="card-info">
            <div className="isExist"><img src={require('../../assets/verified.svg')} alt="yes"/><span>В наличии</span></div>
            <button className="btn-clear small-buy">Купить</button>
          </div>
        </div>
      </div>
    )
  }
}
